  .toggleSwitch {
    width: 50%;
    height: 20px;
    display: block;
    position: relative;
    border-radius: 30px;
    background-color: #fff;
    box-shadow: 0 0 16px 3px rgba(0 0 0 / 15%);
    cursor: pointer;
    margin-top: 0vh;
    margin-left: 25%;
  }

  .toggleSwitch span {
    font-size: 12px;
    padding-right: 0vh;
    padding-top: 0.8vh;
  }


  .toggleSwitch .toggleButton {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50%;
    left: 4px;
    transform: translateY(-50%);
    border-radius: 50%;
    background: #3d8bf0;
  }

  #toggle:checked ~ .toggleSwitch {
    background: #3d8bf0;
  }

  #toggle:checked ~ .toggleSwitch .toggleButton {
    left: calc(100% - 44px);
    background: #fff;
  }

  .toggleSwitch, .toggleButton {
    transition: all 0.2s ease-in;
  }

